/* works.css */

#works {
    min-height: calc(100% - 4rem); /* Add percentage sign here */
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    padding: 0 1rem;
}

.worksTitle {
    margin: 1rem 0;
    font-size: 3rem;
}

.worksDesc {
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;
}

.worksImgs {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}

.worksImg {
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem; /* Add 'rem' unit here */
}

.worksBtn{
    padding: 0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white ;
    font-size: 1rem;
    margin: 3rem 0;
}

@media screen and(max-width:585px) {
    .worksImgs{
        height: 48vw;
    }
    
}
